import Axios from 'axios';
import tempList from '../mock_data'
import {setKeyword} from './keyword_reducer'

const RECEIVE_VIDEO_LIST = 'RECEIVE_VIDEO_LIST';
const GET_VIDEO_LIST = 'GET_VIDEO_LIST';
const CHOICE_VIDEO = 'CHOICE_VIDEO';

const receiveList = (list) => {
    return dispatch=>{
        //ë¦¬ìŠ¤íŠ¸ì¡°íšŒ í›„ ì²«ë²ˆì§¸í•­ëª© ìžë™ìœ¼ë¡œ ìƒë‹¨ì— ì¶œë ¥
        dispatch(choiceVideo(list[0]));
        dispatch(returnList(list));
        
    }
}

const returnList = (list)=>{
    return {
        type: RECEIVE_VIDEO_LIST,
        isFetching: false,
        payload:list
    }
}

export const getList = (keyword = "ë³¼ë§") => {
    return dispatch => {
        // const key = "AIzaSyB2-nh1YNO5HP01iR8Z0m4hkh01l0ST1LY" //1ì°¨ê³„ì •
        const key = "AIzaSyAN-NFQ0XdFkUItbjPlbjd1vLQwki2yHsk" //2ì°¨ê³„ì •
        // const key = "invalid key"
        const type= "video";
        const limit= "18";
        const url = `https://www.googleapis.com/youtube/v3/search?q=${keyword}&type=${type}&part=snippet&maxResults=${limit}&key=${key}`;
        dispatch(setKeyword(keyword));
        Axios.get(url).then((response) => {
            const items =response.data.items;
            dispatch(receiveList(items));
        }
        ).catch(
            (err) => {
                console.error(err);
                alert('youtube api limitì´ˆê³¼ë¡œ ìž„ì‹œë°ì´í„°ë¥¼ í™œìš©í•©ë‹ˆë‹¤.');
                dispatch(receiveList(tempList.items)); // api callì œí•œë•Œë¬¸ì— ìž„ì‹œ ë°ì´í„° ì‚¬ìš©
                return
            }
        )
    }
}
export const choiceVideo = (item) => {
    return {
        type:CHOICE_VIDEO,
        payload:item
    }
}

const initialState={
    list:[],
    choiceVideoId: 'EMPTY'
}

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case RECEIVE_VIDEO_LIST:
            return {
                ...state,
                list: action.payload
            }
        case GET_VIDEO_LIST:
            return {
                ...state
            }
        case CHOICE_VIDEO:
            console.log('choice item',action);
            return {
                ...state,
                item: action.payload
            }
        default:
            return state;
    }
}